import Vuex from 'vuex';

/**
 * Sort the things
 * @param {Array} list
 * @param {Map} lookup
 * @param {Object} data
 * @param {String} name
 */
function sort(list, lookup, data, name) {
  if (lookup.has(name))
    return;

  let recipe = data.recipes[name];
  if (recipe) {
    for (let input of recipe.ingredients) {
      sort(list, lookup, data, input.name);
      lookup.get(input.name).uses.set(name, input.amount);
    }
  }

  list.push(name);
  lookup.set(name, {
    rate: 0,
    uses: new Map(),
  });
}

