export default new Vuex.Store({
  state: {
    data: null,
    sprites: null,

    desired: [],
  },
  getters: {
    data: (s) => s.data,
    sprites: (s) => s.sprites,
    desired: (s) => s.desired,

    item: (s) => (name) => s.data.items[name],

    items(s) {
      let array = [];

      for (let name in s.data.items) {
        array.push(s.data.items[name]);
      }

      return array;
    },

    recipe: (s) => (name) => s.data.recipes[name],

    recipes(s) {
      let array = [];

      for (let name in s.data.recipes) {
        array.push(s.data.recipes[name]);
      }

      return array;
    },

    required(s) {
      let list = [];
      let lookup = new Map();

      for (let desire of s.desired) {
        sort(list, lookup, s.data, desire.name);
        lookup.get(desire.name).rate = 1;
      }

      for (let i = list.length - 1; i >= 0; --i) {
        let name = list[i];
        let rate = lookup.get(name).rate;
        let speed = 0;

        let recipe = s.data.recipes[name];
        if (recipe) {
          speed = 1 / recipe.energy_required;
          for (let input of recipe.ingredients) {
            let inputRate = rate * input.amount;
            lookup.get(input.name).rate += inputRate;
          }
        }

        list[i] = { name, rate, speed };
      }

      let belts = [];
      let index = 0;

      let inputs = [];
      let spans = [];
      let outputs = [];

      for (let i = 0; i < list.length; ++i) {
        let count = 0;

        for (let j = 0; j < belts.length; ++j) {
          if (!belts[j]) continue;

          let info = lookup.get(belts[j].name);
          let use = info.uses.get(list[i].name);
          if (!use) continue;

          outputs.push({
            name: belts[j].name,
            belt: j,
            slot: count++,
            position: i,
          });

          belts[j].rate -= use * list[i].rate;
          if (belts[j].rate == 0) {
            spans.push({
              name: belts[j].name,
              belt: j,
              start: {
                position: belts[j].position,
              },
              finish: {
                slot: count - 1,
                position: i,
              },
            });

            belts[j] = undefined;
            if (j < index)
              index = j;
          }
        }

        belts[index] = {
          name: list[i].name,
          rate: list[i].rate,
          position: i,
        };

        inputs.push({
          name: list[i].name,
          belt: index,
          position: i,
        });

        do ++index;
        while (belts[index] !== undefined);
      }

      for (let j = 0; j < belts.length; ++j) {
        if (!belts[j]) continue;

        spans.push({
          name: belts[j].name,
          belt: j,
          start: {
            position: belts[j].position,
          },
          finish: {
            slot: 0,
            position: list.length,
          },
        });
      }

      console.log(inputs);
      console.log(spans);
      console.log(outputs);

      return { list, inputs, spans, outputs };
    },
  },
  mutations: {
    SET_DATA(state, data) {
      state.data = data;
    },
    SET_SPRITES(state, sprites) {
      state.sprites = sprites;
    },
    SET_DESIRE(state, request) {
      let oldIndex = state.desired.findIndex((a) => a.name == request.name);
      if (oldIndex >= 0) state.desired.splice(oldIndex, 1);

      if (request.rate > 0)
        state.desired.push(request);
    },
  },
});
