<template>
  <div class="group" v-if="subgroups.length > 0">
    <span class="group-title" v-if="debug">{{ group.name }}</span>

    <div class="group-content">
      <div class="subgroup" v-for="subgroup in subgroups" :key="subgroup.name">
        <span class="subgroup-title" v-if="debug">{{ subgroup.name }}</span>

        <div class="subgroup-content">
          <item :name="name" v-for="name in subgroup.items" :key="name" @click="$emit('select', name)"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import Item from '@/components/Item';

export default {
  props: ['group'],

  components: {
    Item,
  },

  data() {
    return {
      debug: false,
    };
  },

  computed: {
    ...mapGetters({
      data: 'data',
      desired: 'desired',
      allItems: 'items',
      getRecipe: 'recipe',
    }),

    subgroups() {
      let array = [];

      for (let name in this.group.subgroups) {
        let items = this.allItems.filter((a) => a.group == this.group.name && a.subgroup == name);

        if (items.length == 0) continue;

        items = items.filter((a) => this.getRecipe(a.name) != null);

        items.sort((a, b) => a.order.localeCompare(b.order));

        array.push({
          name: name,
          items: items.map((a) => a.name),
          order: this.group.subgroups[name],
        });
      }

      return array.sort((a, b) => a.order.localeCompare(b.order));
    },
  },
};
</script>

<style lang="less" module>
.group {
  padding: 4px;
  display: flex;
  flex-direction: column;
}

.subgroup {
  display: flex;
  flex-direction: column;
}

.group-content {
  display: flex;
  flex-direction: column;
}

.subgroup-content {
  display: flex;
  flex-wrap: wrap;
  max-width: (38px * 10);
}
</style>
