<template>
  <div>
    <div class="item-list">
      <group v-for="group in groups" :key="group.name" :group="group" @select="selection = $event"/>
    </div>

    <!-- <div class="belts">
      <div class="input" v-for="input in required.inputs" :key="'i' + input.name" :style="inputStyle(input)" />
      <div class="span" v-for="span in required.spans" :key="'s' + span.name" :style="spanStyle(span)" />
      <div class="output" v-for="output in required.outputs"
        :key="output.position * required.list.length + output.slot" :style="outputStyle(output)" />

    </div> -->

    <!-- <div class="factory">
      <div class="factory-part" v-for="thing in required.list" :key="thing.name">
        <span class="rate">3</span>
        <item :name="thing.name" />
      </div>
    </div> -->

    <crafting-tree :name="selection"/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import Item from '@/components/Item';
import Group from '@/components/Group';
import CraftingTree from '@/components/CraftingTree';

const padd = 6;
const tile = 46;
const marg = 13;

export default {
  name: 'factorio-calc',

  components: {
    Item,
    Group,
    CraftingTree,
  },

  data() {
    return {
      selection: 'copper-plate',
    };
  },

  computed: {
    ...mapGetters({
      data: 'data',
      required: 'required',
    }),

    groups() {
      let array = [];

      for (let id in this.data.groups) {
        array.push(Object.assign({
          name: id,
        }, this.data.groups[id]));
      }

      return array.sort((a, b) => a.order.localeCompare(b.order));
    },
  },

  methods: {
    inputStyle(i) {
      return {
        left: `${tile - padd + tile * i.position}px`,
        height: `${padd + padd * i.belt}px`,
      };
    },
    spanStyle(s) {
      let finish = marg + padd * s.finish.slot + tile * s.finish.position;
      let start = tile - padd + tile * s.start.position;
      let len = finish - start;

      return {
        left: `${start}px`,
        width: `${len + 1}px`,
        top: `${63 + padd + padd * s.belt}px`,
      };
    },
    outputStyle(o) {
      return {
        left: `${marg + padd * o.slot + tile * o.position}px`,
        height: `${padd + padd * o.belt}px`,
      };
    },
  },
};
</script>

