<template>
  <div class="item" @click="$emit('click')" :class="{ active }">
    <div class="icon" :style="iconStyle" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  props: {
    name: String,
  },

  computed: {
    ...mapGetters({
      iconUrl: 'sprites',
      desired: 'desired',
    }),

    item() {
      return this.$store.getters.item(this.name);
    },

    active() {
      return this.desired.find((a) => a.name == this.name) != null;
    },

    iconStyle() {
      return {
        backgroundImage: `url(${this.iconUrl})`,
        backgroundPositionX: `${this.item.icon_col * -2}em`,
        backgroundPositionY: `${this.item.icon_row * -2}em`,
      };
    },
  },

  methods: {
    add() {
      let old = this.desired.find((a) => a.name == this.name);
      let rate = old ? 0 : 1;

      this.$store.commit('SET_DESIRE', {
        name: this.name,
        rate: rate,
      });
    },
  },
};
</script>

<style lang="less" module>
.item {
  margin: 1px;
  padding: 1px;
  border: 1px solid;
  border-top-color: #9c9c9f;
  border-left-color: #9c9c9f;
  border-right-color: #474749;
  border-bottom-color: #474749;
  background: radial-gradient(#6f7172, #595859);

  &:hover, &.active {
    background: radial-gradient(#a87d2e, #8d6327);
    border-top-color: #ca8f38;
    border-left-color: #ca8f38;
    border-right-color: #7b551f;
    border-bottom-color: #7b551f;
  }
}

.icon {
  width: 2em;
  height: 2em;
}
</style>
