import '@babel/polyfill';

import './init';

import Vue from 'vue';
import store from './store';
import App from './App.vue';

const dataSource = require.context('@/data');

const key = 'vanilla-0.16.37';

let data = dataSource(`./${key}.json`);
let sprites = dataSource(`./${key}.png`);

store.commit('SET_DATA', data);
store.commit('SET_SPRITES', sprites);

store.state.desired = [
  { 'name': 'science-pack-1', 'rate': 1 },
  { 'name': 'science-pack-2', 'rate': 1 },
  { 'name': 'science-pack-3', 'rate': 1 },
  { 'name': 'military-science-pack', 'rate': 1 },
  { 'name': 'production-science-pack', 'rate': 1 },
  { 'name': 'high-tech-science-pack', 'rate': 1 },
];

new Vue({
  store: store,
  render: (h) => h(App),
}).$mount('#app');
