<template>
  <div class="tree">
    <crafting-tree-branch :node="root" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import Item from '@/components/Item';
import CraftingTreeBranch from '@/components/CraftingTreeBranch';

function find(name, getters) {
  let node = {
    size: 0,
    name: name,
    children: [],
  };

  for (let recipe of getters.recipes) {
    let input = recipe.ingredients.find((a) => a.name == name);
    if (!input) continue;

    let child = find(recipe.name, getters);
    node.children.push(child);
    node.size += child.size;
  }

  node.children.sort((a, b) => b.size - a.size);

  if (node.size == 0)
    node.size = 1;

  return node;
}

export default {
  props: {
    name: String,
  },

  components: {
    Item,
    CraftingTreeBranch,
  },

  computed: {
    ...mapGetters({
      data: 'data',
    }),

    root() {
      try {
        return find(this.name, this.$store.getters);
      } catch (e) {
        console.log(e);
      }
    },
  },
};
</script>

<style lang="less" module>
.tree {
  padding: 4px;
  display: flex;
}
</style>
